import { Routes, RouterModule }  from '@angular/router';
import { Layout } from './layout.component';
// noinspection TypeScriptValidateTypes
const routes: Routes = [
  { path: '', component: Layout, children: [
    { path: '', redirectTo: 'mockup', pathMatch: 'full' },
    { path: 'dashboard', loadChildren: () => System.import('../dashboard/dashboard.module')  .then(function(module) {    return module['DashboardModule'];  }) },
    { path: 'inbox', loadChildren: () => System.import('../inbox/inbox.module')  .then(function(module) {    return module['InboxModule'];  }) },
    { path: 'charts', loadChildren: () => System.import('../charts/charts.module')  .then(function(module) {    return module['ChartsModule'];  }) },
    { path: 'profile', loadChildren: () => System.import('../profile/profile.module')  .then(function(module) {    return module['ProfileModule'];  }) },
    { path: 'forms', loadChildren: () => System.import('../forms/forms.module')  .then(function(module) {    return module['FormModule'];  }) },
    { path: 'ui', loadChildren: () => System.import('../ui-elements/ui-elements.module')  .then(function(module) {    return module['UiElementsModule'];  }) },
    { path: 'extra', loadChildren: () => System.import('../extra/extra.module')  .then(function(module) {    return module['ExtraModule'];  }) },
    { path: 'tables', loadChildren: () => System.import('../tables/tables.module')  .then(function(module) {    return module['TablesModule'];  }) },
    { path: 'maps', loadChildren: () => System.import('../maps/maps.module')  .then(function(module) {    return module['MapsModule'];  }) },
    { path: 'grid', loadChildren: () => System.import('../grid/grid.module')  .then(function(module) {    return module['GridModule'];  }) },
    { path: 'widgets', loadChildren: () => System.import('../widgets/widgets.module')  .then(function(module) {    return module['WidgetsModule'];  }) },
    { path: 'mockup', loadChildren: () => System.import('../mockup/mockup.module')  .then(function(module) {    return module['MockupModule'];  }) },

  ]}
];

export const ROUTES = RouterModule.forChild(routes);



